body, body ::backdrop {
  --sans-font: -apple-system, BlinkMacSystemFont, "Avenir Next", Avenir, "Nimbus Sans L", Roboto, "Noto Sans", "Segoe UI", Arial, Helvetica, "Helvetica Neue", sans-serif;
  --mono-font: Consolas, Menlo, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  --standard-border-radius: 5px;
  --bg: #fff;
  --accent-bg: #f5f5f5;
  --text: #212121;
  --text-light: #585858;
  --border: #8a8a8a;
  --accent: #4b4b4b;
  --accent-hover: var(--text);
  --accent-text: var(--bg);
  --code: #696969;
  --preformatted: #444;
  --marked: #ccc;
  --disabled: #efefef;
}

@media (prefers-color-scheme: dark) {
  body, body ::backdrop {
    color-scheme: dark;
    --bg: #121212;
    --accent-bg: #1c1c1c;
    --text: #dcdcdc;
    --text-light: #ababab;
    --accent: #b5b5b5;
    --accent-hover: var(--text);
    --accent-text: var(--bg);
    --code: #ccc;
    --preformatted: #ccc;
    --disabled: #111;
  }
}

.subtle {
  opacity: .8;
  font-style: italic;
}

body h1 {
  font-size: 2rem;
}

body h2 {
  font-size: 1.2rem;
}

ul {
  padding: unset;
  list-style-type: none;
}

.action {
  text-align: center;
  background-color: var(--bg);
  color: var(--text);
}

.action:hover {
  color: var(--bg);
}

.action:hover svg {
  fill: var(--bg);
}

.action svg {
  fill: var(--text);
  vertical-align: middle;
  height: 1lh;
}

dialog {
  min-width: min(45rem, 90%);
}

.notice--assertive {
  --accent-bg: lightpink;
  --border: indianred;
}

.float-right {
  float: right;
}

body:has(dialog[open]) {
  overflow: hidden;
}

.list-item details {
  margin-bottom: 0;
}

.list-item details:not(:hover) {
  background: none;
  border-color: #0000;
}

.list-item details > summary {
  font-weight: normal;
  list-style: none;
}

.list-item details > summary::marker {
  display: none;
}

.list-item details > summary::-webkit-details-marker {
  display: none;
}
